* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
}

body {
    height: 100vh; 
    background-color: gray;
}

ion-icon {
    font-size: 30px;
    cursor: pointer;
}

/* Se rolagem mobile não funcionar, acionar essa opção, juntamente com a do arquivo css do MyFabric*/
canvas {
    touch-action: manipulation;
}

.canvas-container .upper-canvas :active {
    background-color: rgba(255,255,255,0.0) !important;
}

.my_fabric .upper-canvas:active {
    background-color: rgba(0,0,0,0);
}

/*Import das fontes*/

/*Roboto*/
@font-face {
    font-family: Roboto;
    src: url('./assets/fonts/Roboto/roboto-regular.woff2');
}

@font-face {
    font-family: 'Roboto Italic';
    src: url('./assets/fonts/Roboto/roboto-italic-webfont.woff2');
}

@font-face {
    font-family: 'Roboto Bold';
    src: url('./assets/fonts/Roboto/roboto-bold-webfont.woff2');
}

@font-face {
    font-family: 'Roboto Bold Italic';
    src: url('./assets/fonts/Roboto/roboto-bolditalic-webfont.woff2');
}

/*Poppins*/
@font-face {
    font-family: Poppins;
    src: url('./assets/fonts/Poppins/poppins-regular-webfont.woff2');
}

@font-face {
    font-family: 'Poppins Italic';
    src: url('./assets/fonts/Poppins/poppins-italic-webfont.woff2');
}

@font-face {
    font-family: 'Poppins Bold';
    src: url('./assets/fonts/Poppins/poppins-bold-webfont.woff2');
    font-weight: bold;
}

@font-face {
    font-family: 'Poppins Bold Italic';
    src: url('./assets/fonts/Poppins/poppins-bolditalic-webfont.woff2') format('woff2');
    font-weight: bold;
    font-style: italic;
}

